.timeline {
  padding: 32px 40px;

  .firstRow {
    display: grid;
    grid-template-columns: repeat(8, 1fr);

    & > div {
      width: 100px;
      height: 100px;
      border-radius: 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-weight: bold;
      font-size: 18px;
      color: #FFF;
      background: green;
    }
  }
}
