.piningPanels {

  .top {
    height: 25vh;
    background: grey;
  }

  .block1 {
    height: 250px;
    background: red;
    color: #FFF;
    position: relative;
    z-index: 1;
  }

  .block2 {
    height: 250px;
    background: blue;
    color: #FFF;
    position: relative;
    z-index: 2;
  }

  .block3 {
    height: 250px;
    background: lime;
    color: #FFF;
    position: relative;
    z-index: 3;
  }

  .bottom {
    height: 200vh;
    background: grey;
  }
}
