@import "../../assets/style/app";

.runningString {
  position: relative;

  .row {
    background: #286228;
    position: absolute;
    left: 0;
    width: 100%;

    .rowItem {
      height: 100%;
      display: flex;
      align-items: center;
      color: orange;
      text-transform: uppercase;

      .text {
        @include TitanOne;
      }
    }
  }

  .column {
    position: absolute;
    z-index: 2;
    top: 0;
    height: calc(100vh - 40px);
    background: orange;

    @include desktop {
      height: calc(100vh - 60px);
    }

    .columnItem {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      color: #286228;
      text-transform: uppercase;

      .text {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;

       span {
         @include TitanOne;
         text-align: center;
       }
      }
    }
  }

}