@import "../../assets/style/app";

.waveEnteringTitle {
  box-sizing: border-box;
  padding: 40px;
  background: #c2f1ba;

  .title {
    //text-transform: uppercase;
    font-size: 40px;
    color: darkblue;
    display: flex;
    justify-content: center;

    @include desktop {
      font-size: 80px;
    }

    &_2 {
      margin-top: calc(100vh - 60px - 40px - 80px + 0px);
    }

    &_3 {
      margin-top: 200px;
    }

    &_4 {
      margin-top: 200px;
    }

    &_5 {
      margin-top: 200px;
      margin-bottom: 50vh;
    }

    .empty {
      width: 18px;
    }
  }

  .label {
    margin-top: 48px;
    font-size: 48px;
    color: rgba(0, 0, 0, 0.1);
    text-align: center;
  }

  .arrow {
    width: 50px;
    height: 50px;
    border-right: 10px solid rgba(0, 0, 0, 0.1);
    border-bottom: 10px solid rgba(0, 0, 0, 0.1);
    transform: rotate(45deg);
    margin-left: auto;
    margin-right: auto;
  }

}
