.skewOnScroll {
  padding-top: 32px;

  .imgs {
    display: grid;
    grid-template-columns: repeat(2, 600px);
    grid-template-rows: repeat(4, 400px);
    grid-gap: 8px;
    width: 1208px;
    margin-left: auto;
    margin-right: auto;

    img {
      display: block;
      width: 600px;
      height: 400px;
      object-fit: cover;
    }
  }

}
