@import "../../assets/style/app";

.stepByStep {
  @include container;
  position: relative;
  box-sizing: border-box;

  .inner {
    @include inner;
    position: relative;

    .beforeWrapper {
      height: calc(100vh - 40px);
      box-sizing: border-box;
      padding: 16px 0;

      @include desktop {
        padding: 32px;
        height: calc(100vh - 60px);
      }
    }

    .wrapper {
      height: 100vh;
      box-sizing: border-box;
      padding: 16px 0;

      @include desktop {
        padding: 32px;
      }
    }

    .back {
      height: 100%;
      width: 100%;
      background: green;
      position: relative;
      z-index: 1;
      border-radius: 16px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
      box-sizing: border-box;
      padding-top: 32px;
      padding-bottom: 32px;

      @include desktop {
        padding-top: 64px;
        padding-bottom: 64px;
      }

      p {
        font-size: 32px;
        color: rgba(255,255,255,0.1);
        text-transform: uppercase;

        @include desktop {
          font-size: 64px;
        }
      }
    }

    .before {
      height: 100%;
    }

    .steps {
      position: relative;
      z-index: 2;

      .step {
        @include centerRow;
        height: 100vh;

        p {
          padding: 8px 32px;
          background: darkorchid;
          font-size: 40px;
          text-transform: uppercase;
          font-weight: bold;
          border-radius: 999px;
          color: #FFF;

          @include desktop {
            font-size: 80px;
            padding: 16px 64px;
          }
        }
      }
    }


  }
}