.basic {
  padding: 40px;
  display: flex;
  flex-direction: column;

  .firstRow {
    align-self: center;
    display: flex;
    align-items: center;
    justify-content: center;

    .first {
      position: relative;
      padding: 32px;
      border: 5px dashed grey;
      border-radius: 10px;
      display: flex;
      justify-content: center;
      box-sizing: border-box;

      .label {
        position: absolute;
        top: 0;
        left: 16px;
        transform: translateY(-65%);
        font-weight: bold;
        font-size: 24px;
        background: #FFF;
        padding: 0px 4px;
      }

      & > div {
        width: 120px;
        height: 120px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: bold;
        font-size: 18px;
        color: #FFF;

        &:not(:first-child) {
          margin-left: 100px;
        }
      }

      .blue {
        border-radius: 20px;
        background: blue;
      }

      .box {
        border-radius: 20px;
        background: green;
      }

      .circle {
        border-radius: 50%;
        background: red;
      }
    }

    .outside_blue {
      border-radius: 20px;
      background: blue;
      width: 120px;
      height: 120px;
      box-sizing: border-box;
      padding: 0px 8px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-weight: bold;
      font-size: 18px;
      color: #FFF;
      text-align: center;
      margin-right: auto;
      margin-left: 40px;
    }
  }

  .secondRow {
    margin-top: 40px;
    display: flex;
    justify-content: center;

    & > div {
      width: 100px;
      height: 100px;
      border-radius: 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-weight: bold;
      font-size: 18px;
      color: #FFF;

      &:not(:first-child) {
        margin-left: 100px;
      }
    }

    .to {
      background: blueviolet;
    }

    .from {
      background: brown;
    }

    .fromTo {
      background: chocolate;
    }

    .set {
      background: lightgreen;
    }
  }

  .thirdRow {
    margin-top: 140px;
    display: flex;
    justify-content: center;

    & > div {
      width: 100px;
      height: 100px;
      border-radius: 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-weight: bold;
      font-size: 18px;
      color: #FFF;

      &:not(:first-child) {
        margin-left: 100px;
      }
    }

    .once {
      background: blueviolet;
    }

    .repeat {
      background: brown;
    }

    .infinitely {
      background: chocolate;
    }

    .yoyo {
      background: cornflowerblue;
    }
  }

  .fourRow {
    margin-top: 140px;
    display: flex;
    justify-content: center;

    & > div {
      width: 100px;
      height: 100px;
      border-radius: 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-weight: bold;
      font-size: 18px;
      color: #FFF;

      &:not(:first-child) {
        margin-left: 100px;
      }
    }

    .boxAnimLeft {
      background: darkgreen;
    }

    .boxAnimRight {
      background: brown;
    }
  }

}
