.gradientAnimation {
  padding: 64px;

  .target {
    cursor: pointer;
    height: 200px;
    border-radius: 10px;
    background: linear-gradient(90deg, green, red);

    display: flex;
    justify-content: center;
    align-items: center;

    & > p {
     font-size: 40px;
      text-transform: uppercase;
      color: rgba(255, 255, 255, 0.5);
    }
  }
}
