.modifiers {
  background: green;
  min-height: calc(100vh - 60px);

  .arrow {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    .dot {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 24px;
      height: 24px;
      background: #000;
      border-radius: 50%;
    }

    .body {
      position: absolute;
      height: 12px;
      width: 200px;
      background: #000;
      transform: translateY(-50%);
    }

    .tip {
      position: absolute;
      top: -20px;
      left: 165px;
      width: 40px;
      height: 40px;
      box-sizing: border-box;
      border: 10px solid #000;
      border-left: none;
      border-top: none;
      transform: rotate(-45deg);
    }
  }

}
