.control {
  padding: 32px 40px;

  .buttons {
    display: flex;

    button {
      &:not(:first-child) {
        margin-left: 16px;
      }
    }
  }

  .box {
    margin-top: 60px;
    width: 100px;
    height: 100px;
    border-radius: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    font-size: 18px;
    color: #FFF;
    background: green;
  }
}
