.scrollPin {
  padding-top: 100vh;
  padding-left: 32px;
  padding-right: 32px;
  padding-bottom: 100vh;

  .box1, .box2 {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 200px;
    height: 100px;
    color: #FFF;
    font-weight: bold;
  }

  .box1 {
    background: green;
  }

  .box2 {
    background: blue;
  }

  .box3 {
    background: lightcoral;
  }
}
