@import "../../assets/style/app";

.parallaxImages {
  @include container;

  .inner {
    @include inner;

    .beforeWrapper {
      height: calc(100vh - 40px);
      box-sizing: border-box;
      padding: 16px 0;

      @include desktop {
        padding: 32px;
        height: calc(100vh - 60px);
      }

      .before {
        height: 100%;
      }
    }

    .wrapper {
      height: 100vh;
      box-sizing: border-box;
      padding: 16px 0;

      @include desktop {
        padding: 32px;
      }
    }
  }

  .items {
    display: flex;
    flex-direction: column;

    .item {
      position: relative;
      width: 200px;
      height: 200px;
      //border: 1px solid red;
      //box-sizing: border-box;
      overflow: hidden;

      @include desktop {
        width: 400px;
        height: 400px;
      }

      &:not(:first-child) {
        margin-top: 24px;

        @include desktop {
          margin-top: 40px;
        }
      }

      &:nth-child(even) {
        align-self: flex-end;
      }

      img {
        position: absolute;
        top: -200px;
        left: 0;
        width: 200px;
        height: 400px;
        object-fit: cover;

        @include desktop {
          top: -400px;
          left: 0;
          width: 400px;
          height: 800px;
        }

      }

    }
  }
}