@import "../../assets/style/app";

.header {
  @include container;
  height: 40px;
  display: flex;
  align-items: center;
  background: royalblue;

  @include desktop {
    height: 60px;
  }

  .inner {
    @include inner;
    display: flex;
    align-items: center;
    height: 100%;

    .logo {
      font-size: 24px;
      color: #FFF;
      text-decoration: none;
      transition: 0.3s;

      @include desktop {
        font-size: 36px;
      }


      &:hover {
        @include desktop {
          color: lime;
        }
      }
    }

  }


}
