@import "../../assets/style/app";

.stickyPanel {
  padding: 32px;

  .blockBefore {
    background: #AAA;
    height: 300px;
    border-radius: 16px;

    @include desktop {
      height: 400px;
    }
  }

  .wrapper {
    padding-top: 24px;
    display: flex;

    .panel {
      flex-shrink: 0;
      align-self: flex-start;
      width: 300px;
      box-sizing: border-box;
      padding: 16px;
      border-radius: 16px;
      background: darkgreen;

      p {
        font-size: 24px;
        color: #FFF;

        &:not(:first-child) {
          margin-top: 8px;

        }
      }
    }

    .content {
      margin-left: 32px;
      background: #AAA;
      padding: 16px;
      border-radius: 16px;

      @include desktop {
        padding: 24px;
      }

      p {
        font-size: 24px;
      }

      p:nth-child(2) {
        @include onlyDesktop();
      }
    }
  }

  .after {
    margin-top: 24px;
    background: #AAA;
    height: 100vh;
  }
}
