@import "../../../assets/style/app";

.before {
  @include centerColumn;
  border-radius: 16px;
  background: #AAA;
  height: 100%;

  .title {
    font-size: 50px;
    text-transform: uppercase;

    @include desktop {
      font-size: 100px;
    }
  }

  .label {
    font-size: 24px;
    margin-top: 8px;

    @include desktop {
      font-size: 48px;
      margin-top: 16px;
    }
  }

  .arrow {
    width: 25px;
    height: 25px;
    border-style: solid;
    border-width: 5px;
    border-left: none;
    border-top: none;
    transform: rotate(45deg);

    @include desktop {
      width: 50px;
      height: 50px;
      border-width: 10px;
    }
  }
}