@import "../../assets/style/app";

.codeEditor {
  @include container;
  padding-top: 32px;
  padding-bottom: 32px;

  @include desktop {
    padding-top: 64px;
    padding-bottom: 64px;
  }

  .inner {
    @include inner;

    .editor {
      background: #171108;
      border-radius: 8px;


      .top {
        border-bottom: 1px solid rgba(255, 255, 255, 0.25);
        display: flex;
        align-items: center;
        padding: 16px;

        @include desktop {
          padding: 24px;
        }

        p {
          @include RobotoMono;
          color: #FFF;
          font-size: 14px;

          @include desktop {
            font-size: 18px;
          }
        }

        .closeIcon {
          color: rgba(255, 255, 255, 0.25);
          font-size: 14px;
          margin-left: 7px;

          @include desktop {
            font-size: 18px;
            margin-left: 9px;
          }
        }
      }

      .content {
        padding: 16px;

        @include desktop {
          padding: 24px;
        }

        .stringsWrapperMobile {
          //@include onlyTabletAndLess;
        }

        .string {
          position: relative;

          &:nth-child(3),
          &:nth-child(8) {
            margin-top: 14px;

            @include desktop {
              margin-top: 18px;
            }
          }

          &:nth-child(4),
          &:nth-child(5),
          &:nth-child(6),
          &:nth-child(9),
          &:nth-child(15),
          &:nth-child(16),
          &:nth-child(18),
          &:nth-child(19) {
            margin-left: 14px;

            @include desktop {
              margin-left: 18px;
            }
          }

          &:nth-child(10),
          &:nth-child(11),
          &:nth-child(14),
          &:nth-child(17) {
            margin-left: 28px;

            @include desktop {
              margin-left: 36px;
            }
          }

          &:nth-child(12),
          &:nth-child(13) {
            margin-left: 42px;

            @include desktop {
              margin-left: 56px;
            }
          }

        }

        .letter {
          @include RobotoMono;
          opacity: 0;
          color: #FFF;
          font-size: 14px;

          @include desktop {
            font-size: 18px;
          }

          &_1 {
            color: #c4c445;
          }

          &_2 {
            color: #5abe5a;
          }
        }

        .empty {
          display: inline-block;
        }

        .pointer {
          opacity: 0;
          position: absolute;
          height: 100%;
          top: 0;
          left: 0;
          width: 9px;

          .pointerInner {
            width: 100%;
            height: 100%;
            background: #FFF;

            @include desktop {
              width: 11px;
            }
          }
        }


      }
    }
  }


}