@import "../../assets/style/app";

.flowText {
  position: relative;
  overflow: hidden;

  .fixedLetter {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    //@include TitanOne;
    font-weight: bold;
    font-size: 48px;
    color: red;

    @include desktop {
      font-size: 96px;
    }

  }

  .texts {
    display: flex;
    justify-content: center;
    margin-top: 75vh;
    margin-bottom: 100vh;

    .part {
      display: flex;
      flex-direction: column;

      &:nth-child(1) {
        align-items: flex-end;
      }

      &:nth-child(2) {
        align-items: flex-start;
        margin-left: 16px;

        @include desktop {
          margin-left: 32px;
        }

      }

      .text {
        font-size: 24px;

        @include desktop {
          font-size: 48px;
        }
      }
    }

  }
}