.anchorNavigation {
  overflow-x: hidden;

  .navigations {
    position: fixed;
    bottom: 0;
    left: 0;
    padding: 8px 16px;
    background: rgba(0, 0, 0, 0.5);
    border-radius: 0 8px 0 0;

    & > a {
      color: #FFF;
      font-size: 18px;

      &:not(:first-child) {
        margin-left: 16px;
      }
    }
  }

  .intro {
    width: 100vw;
    height: calc(100vh - 60px);
    background: cornflowerblue;
    display: flex;
    align-items: center;
    justify-content: center;

    h1 {
      font-size: 64px;
    }
  }

  .panelsContainer {
    width: 500%;
    display: flex;
    flex-wrap: nowrap;
    padding: 0;
    overflow: hidden;

    .panel {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 100vh;
      overflow: hidden;

      &1 {
        background: lightcoral;
      }

      &2 {
        background: orange;
      }

      &3 {
        background: blueviolet;
      }

      &4 {
        background: greenyellow;
      }

      &5 {
        background: grey;
      }

      h3 {
        font-size: 48px;
      }

      .nav {
        position: absolute;
        bottom: 32px;
        right: 48px;
        display: flex;

        .next, .prev {
          text-transform: uppercase;
          font-size: 32px;
          color: #FFF;
        }

        .next {
          margin-left: 32px;
        }

      }
    }
  }

  .map {
    width: 100vw;
    height: 100vh;
    background: darkgreen;
    display: flex;
    align-items: center;
    justify-content: center;


    h3 {
      font-size: 48px;
    }

  }


}
