@import "../../assets/style/app";

.stepToStep {
  @include container;

  .inner {
    @include inner;

    .beforeWrapper {
      height: calc(100vh - 40px);
      box-sizing: border-box;
      padding: 16px 0;

      @include desktop {
        padding: 32px;
        height: calc(100vh - 60px);
      }

      .before {
        height: 100%;
      }
    }

    .wrapper {
      height: 100vh;
      box-sizing: border-box;
      padding: 16px 0;

      @include desktop {
        padding: 32px;
      }
    }

    .back {
      height: 100%;
      width: 100%;
      background: green;
      position: relative;
      z-index: 1;
      border-radius: 16px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
      box-sizing: border-box;
      padding-top: 16px;
      padding-bottom: 16px;

      @include desktop {
        padding-top: 32px;
        padding-bottom: 32px;
      }

      p {
        font-size: 32px;
        color: rgba(255,255,255,0.1);
        text-transform: uppercase;

        @include desktop {
          font-size: 64px;
        }
      }
    }

    .steps {
      position: relative;
      z-index: 2;

      .step {
        display: flex;
        justify-content: center;
        height: 100vh;
        box-sizing: border-box;
        //background: grey;
        //border: 10px solid #FFF;

        p {
          display: flex;
          align-items: center;
          padding: 0 32px;
          background: darkorchid;
          font-size: 40px;
          text-transform: uppercase;
          font-weight: bold;
          border-radius: 999px;
          color: #FFF;
          height: calc(100vh / 7);

          @include desktop {
            font-size: 80px;
            padding: 0 64px;
          }
        }
      }
    }

  }
}