@import "../../assets/style/app";

.home {
  @include container;
  padding-top: 24px;
  padding-bottom: 24px;

  @include desktop {
    padding-top: 40px;
    padding-bottom: 40px;
  }

  .inner {
    @include inner;
    display: flex;

    .contentMobile {
      @include onlyTabletAndLess();
      width: 100%;

      .switcher {
        position: relative;
        width: 100%;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        border: 2px solid purple;
        box-sizing: border-box;
        border-radius: 8px;

        &::after {
          position: absolute;
          display: block;
          content: "";
          top: 0;
          left: 0;
          bottom: 0;
          width: 50%;
          background: purple;
          transition: 0.3s;
        }

        &_right {
          &::after {
            transform: translateX(100%);
          }

        }

        .btn {
          @include button;
          position: relative;
          z-index: 2;
          padding: 4px 0;

          span {
            font-size: 18px;
            font-weight: bold;
            transition: 0.3s;
            color: #000;
          }

          &_selected {
            span {
              color: #FFF;
            }

          }
        }

      }


    }

    .contentDesktop {
      @include onlyDesktop(grid);
      grid-template-columns: repeat(2, 1fr);

      .column {

        &:not(:first-child) {
          margin-left: 48px;
        }

        .label {
          font-size: 24px;
          font-weight: bold;
        }
      }
    }

    .links {
      margin-top: 16px;
      display: flex;
      flex-direction: column;

      @include desktop {
        margin-top: 24px;
      }

      .link {
        color: darkblue;
        font-weight: bold;
        font-size: 18px;
        transition: 0.3s;

        &:not(:first-child) {
          margin-top: 4px;
        }

        &:hover {
          color: #007300;
        }
      }
    }

  }



}
