.scroll {
  padding-top: 100vh;
  padding-left: 32px;
  padding-right: 32px;
  padding-bottom: 100vh;

  .box1, .box2, .box3 {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 200px;
    height: 100px;
    color: #FFF;
    font-weight: bold;
  }

  .box1 {
    background: green;
  }

  .box2 {
    background: blue;
    margin-top: 100px;
    margin-bottom: 100px;
  }

  .box3 {
    background: lightcoral;
  }

  .progress {
    position: fixed;
    bottom: 0;
    right: 0;
  }
}
