.wrap {
  box-sizing: border-box;
  min-height: calc(100vh - 60px);

  .ballWrap, .ballWrapYoyo {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-transform: uppercase;
    color: #FFF;
  }

  .ballWrap {
    background: cornflowerblue;
  }

  .ballWrapYoyo {
    background: darkorange;
    margin-top: 50px;
  }
}
