.timelineCircle {
  min-height: calc(100vh - 60px);
  position: relative;

  .itemWrapper {
    position: absolute;
    top: 50vh;
    left: 50vw;

    .item {
      width: 50px;
      height: 50px;
      border-radius: 50%;
      background: green;
      position: absolute;
    }
  }



}
